body {
  font-family: $body-font-family;
  font-size: 2.75em;

  .theme-light {
    color: $theme_light_body;
    @include text-shadow(1px, 1px, 1px, #000);
    h1,
    #spinner {
      color: $theme_light_h1;
    }
  }

  @include media(small-only) {
    font-size: 1.5em;
  }
  a,
  .ctl {
    text-decoration: none;
    transition: all 0.5s ease;
    color: $link_orange;
    &:hover {
      color: $link_yellow;
      text-shadow: $glow-effect;
      cursor: pointer;
    }
  }
  h1 {
    font-size: 3.5em;

    @include media(small-only) {
      font-size: 2.5em;
    }
  }
  h2 {
    font-size: 1.25em;
  }
  h3 {
    font-size: 1.1em;
  }
}
.footer {
  //position: fixed;
  //bottom: 0;
  width: 100%;
  text-align: right;
  font-family: $overlay-body-font-family;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .copyright {
    padding: 1rem;
    color: $link_color;
    @include text-shadow(2px, 1px, 1px, #000);
  }
}

h4 {
  font-size: 1em;
  font-style: oblique;
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: $icon-font-family;
  speak-as: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

