/*
 * Material Design Progress Bar
 *
 * See following StackOverflow link:
 * https://stackoverflow.com/questions/34773266/how-to-write-css-keyframes-to-indeterminate-material-design-progress-bar
 * as well as CodePen:
 * https://codepen.io/shalimano/pen/wBmNGJ
 */

.mdprogressbar {
  //position: absolute;
  width: 100%;
  height: $progress-bar-height;
  overflow-x: hidden;
  .line {
    position: absolute;
    opacity: 0.4;
    background: #4a8df8;
    width: 150%;
    //height: 100%;
  }

  .subline {
    position: absolute;
    background: #4a8df8;
    height: $progress-bar-height;
  }
  .inc {
    animation: increase 2s infinite;
  }
  .dec {
    animation: decrease 2s 0.5s infinite;
  }
}

@keyframes increase {
  from {
    left: -5%;
    width: 5%;
  }
  to {
    left: 130%;
    width: 100%;
  }
}
@keyframes decrease {
  from {
    left: -80%;
    width: 80%;
  }
  to {
    left: 110%;
    width: 10%;
  }
}
